import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { HttpClientModule, HTTP_INTERCEPTORS, HttpClient } from '@angular/common/http';
import { InterceptorProvider } from '../providers/interceptor/interceptor';
import { AuthProvider } from '../providers/auth/auth';
import { IonicStorageModule } from '@ionic/storage';
import { Camera } from '@ionic-native/camera';
import { AdsProvider } from '../providers/ads/ads';
import { CodesProvider } from '../providers/codes/codes';
import { BarcodeScanner } from '@ionic-native/barcode-scanner';
import { BuyingProvider } from '../providers/buying/buying';
import { SurveyProvider } from '../providers/survey/survey';

@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/add-ad/add-ad.module.ngfactory#AddAdPageModuleNgFactory', name: 'AddAdPage', segment: 'add-ad', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module.ngfactory#HomePageModuleNgFactory', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module.ngfactory#LoginPageModuleNgFactory', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/my-ads/my-ads.module.ngfactory#MyAdsPageModuleNgFactory', name: 'MyAdsPage', segment: 'my-ads', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/profile/profile.module.ngfactory#ProfilePageModuleNgFactory', name: 'ProfilePage', segment: 'profile', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/register/register.module.ngfactory#RegisterPageModuleNgFactory', name: 'RegisterPage', segment: 'register', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/requests/requests.module.ngfactory#RequestsPageModuleNgFactory', name: 'RequestsPage', segment: 'requests', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/survey/survey.module.ngfactory#SurveyPageModuleNgFactory', name: 'SurveyPage', segment: 'survey', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tabs/tabs.module.ngfactory#TabsPageModuleNgFactory', name: 'TabsPage', segment: 'tabs', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/view-ad/view-ad.module.ngfactory#ViewAdPageModuleNgFactory', name: 'ViewAdPage', segment: 'view-ad', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/view-request/view-request.module.ngfactory#ViewRequestPageModuleNgFactory', name: 'ViewRequestPage', segment: 'view-request', priority: 'low', defaultHistory: [] }
  ]
}),
    IonicStorageModule.forRoot(),
    HttpClientModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    InterceptorProvider,
    { provide: HTTP_INTERCEPTORS, useClass: InterceptorProvider, multi: true },
    AuthProvider,
    Camera,
    AdsProvider,
    CodesProvider,
    BarcodeScanner,
    BuyingProvider,
    SurveyProvider
  ]
})
export class AppModule {}



